import {DirectiveResolver} from '@angular/compiler';
import {Directive, Type} from '@angular/core';

const /** @type {?} */ COMPONENT_SELECTOR = /^[\w|-]*$/;
const /** @type {?} */ directiveResolver = new DirectiveResolver();

export interface AttrProp {
  prop: string;
  attr: string;
  bracketAttr: string;
  bracketParenAttr: string;
  parenAttr: string;
  onAttr: string;
  bindAttr: string;
  bindonAttr: string;
}

export interface ComponentInfo {
  type: Type<any>;
  selector: string;
  inputs?: AttrProp[];
  outputs?: AttrProp[];
}
/**
 * @param {?} type
 * @return {?}
 */
export function getComponentInfo(type: Type<any>): ComponentInfo {
  const /** @type {?} */ resolvedMetadata: Directive = directiveResolver.resolve(type);
  const /** @type {?} */ selector = resolvedMetadata.selector;
  return {
    type: type,
    selector: selector,
    inputs: parseFields(resolvedMetadata.inputs),
    outputs: parseFields(resolvedMetadata.outputs)
  };
}
/**
 * @param {?} names
 * @return {?}
 */
export function parseFields(names: string[]): AttrProp[] {
  const /** @type {?} */ attrProps: AttrProp[] = [];
  if (names) {
    for (let /** @type {?} */ i = 0; i < names.length; i++) {
      const /** @type {?} */ parts = names[i].split(':');
      const /** @type {?} */ prop = parts[0].trim();
      const /** @type {?} */ attr = (parts[1] || parts[0]).trim();
      const /** @type {?} */ capitalAttr = attr.charAt(0).toUpperCase() + attr.substr(1);
      attrProps.push( /** @type {?} */((<AttrProp>{
        prop: prop,
        attr: attr,
        bracketAttr: `[${attr}]`,
        parenAttr: `(${attr})`,
        bracketParenAttr: `[(${attr})]`,
        onAttr: `on${capitalAttr}`,
        bindAttr: `bind${capitalAttr}`,
        bindonAttr: `bindon${capitalAttr}`
      })));
    }
  }
  return attrProps;
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
